<template>
  <header class="header">
    <div class="logo">
    </div>
    <nav class="navRouter" @click="clickTabFn">
        <router-link :class="{selectedTab:selectedTabName === 'Home'}" to="/home"><span>Home</span></router-link>
        <router-link :class="{selectedTab:selectedTabName === 'Blog'}" to="/blog"><span>Blog</span></router-link>
        <router-link :class="{selectedTab:selectedTabName === 'Project'}" to="/project"><span>Project</span></router-link>
        <router-link :class="{selectedTab:selectedTabName === 'About'}" to="index"><span>About</span></router-link>
    </nav>
  </header>
</template>

<script>
    export default{
        data() {
          return{
            selectedTabName:''
          }
        },
        methods: {
          clickTabFn(event){
            let target = event.target;
            if(target.nodeName.toLowerCase() === 'a'){
              this.selectedTabName = target.text;
            }
          }
        }
    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .header{
    min-height:0.7rem;
    overflow:hidden;
    .navRouter{
      width:5rem;
      display:flex;
      font-weight:700;
      position:absolute;
      a{
        flex:0.25;
        display:flex;
        width:0.4rem;
        height:0.26rem;
        align-items:flex-end;
        padding:0.2rem 0;
        margin:0 0.2rem;
        font-size:0.18rem;
        transition: height 0.5s ease-out,font-size 0.5s;
        span{
          margin:0 auto;
        }
      }
      /*a:hover{*/
        /*height:0.8rem;*/
      /*}*/
      /*.selectedTab{*/
        /*height:0.6rem;*/
        /*font-size:0.25rem;*/
      /*}*/
    }
  }
</style>



