<template>
  <div class="main">
    <navBar></navBar>
    <appMain></appMain>
    <footer class="footerContent">
      <div>
        <i class="iconfont icon-github"></i>
        <a href="https://github.com/orozot" target="_blank"><span style="color:#c9cacc">@orozot</span></a>
      </div>
    </footer>
  </div>
</template>

<script>
  import navBar from '@/components/navBar'
  import appMain from '@/components/appMain'
  export default{
    components:{
      navBar,
      appMain
    }
  }

</script>

<style lang='scss' scoped>
  .main{
    .footerContent{
      margin-bottom:0.1rem;
      position:fixed;
      bottom:0.1rem;
      left:0;
      right:0;
    }
  }
</style>



